<template>
  <div id="home">
    <div class="left_menu">
        <img class="headPortrait" @click="logob=!logob" src="../assets/images/logo.png" alt="">
        <div class="logoBox" v-show="logob==true">
            <img class="imgq" src="../assets/images/箭头 三角形.png" alt="">
            <!-- <img class="imgq" src="../assets/images/箭头 三角形.png" alt=""> -->
            <div class="survey">
                <img class="UserAvatar" src="../assets/images/company.png" alt="">
                <!-- <el-upload
                class="avatar-uploader"
                :show-file-list="false"
                action
                :http-request="handleAvatarSuccess"
                :on-preview="handlePreview"
                >
                    <img v-if="imageUrl" :src="imageUrl" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload> -->
                <p class="userName">{{namer}}</p>
            </div>
            <ul class="obList">
                <li @click="logo()">
                    退出登录
                </li>
            </ul>
        </div>
        <ul class="nav" @click="logob=false">
            <li @click="routeto('/home/taskList')" :class="{ass:nav=='/home/taskList'}">
                <img v-show="nav=='/home/taskList'" src="../assets/images/任务.png" alt="">
                <img v-show="nav!='/home/taskList'" src="../assets/images/任务 (1).png" alt="">
                <span>任务</span>
            </li>
            <li @click="routeto('/home/Myequipment')" :class="{ass:nav=='/home/Myequipment'}">
                <img v-show="nav=='/home/Myequipment'" src="../assets/images/设备.png" alt="">
                <img v-show="nav!='/home/Myequipment'" src="../assets/images/设备 (1).png" alt="">
                <span>设备</span>
            </li>
            <li @click="routeto('/home/upload')" :class="{ass:nav=='/home/upload'}">
                <img v-show="nav=='/home/upload'" src="../assets/images/数据上传.png" alt="">
                <img v-show="nav!='/home/upload'" src="../assets/images/数据上传 (1).png" alt="">
                <span>上传</span>
            </li>
        </ul>
    </div>
    <div class="right_body" @click="logob=false">
        <router-view></router-view>
    </div>
  </div>
</template>

<script>
export default {
  name: "home",
  props: {
    
  },
  data(){
      return{
          nav:'/home/taskList',
          logob:false,
          imageUrl:'',
          namer:'系统管理员'
      }
  },
  methods:{
      routeto(to){
          this.nav=to
          this.$router.push(to)
      },
      logo(){
        this.$confirm('您确定要退出吗？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          confirmButtonClass: "butText",
        }).then(() => {
          this.$message({
            type: 'success',
            message: '已退出登录!'
          });
          this.routeto('/','0')
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          });          
        });
      },
      handleAvatarSuccess(params, fileList) {
        // this.imageUrl = params.file;
        // console.log(params);
      },
      handlePreview(file, fileList){
        console.log(file, fileList)
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg/png/jpg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      }
  },
  mounted(){
    this.namer=this.$store.state.userName
  },
  watch: {
      
    $route(){
      this.nav=this.$store.state.nav    
    }
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
#home{
    width: 100%;
    height: 100%;
    overflow: auto;
    .left_menu{
        width: 60px;
        height: 100%;
        float: left;
        background: #2a2b2c;
        position: relative;
        .logoBox{
            width: 150px;
            position: absolute;
            top: 10px;
            right: -153px;
            background: #fff;
            border: 1px solid #ccc;
            border-radius: 8px;
            z-index: 999;
            .survey{
                height: 51px;
                width: 100%;
                background: #005650;
                border-radius: 8px 8px 0 0;
                // display: flex;
                overflow: hidden;
                .UserAvatar{
                    width: 30px;
                    height: 30px;
                    margin: 8px;
                    float: left;
                }
                .userName{
                    float: left;
                    font-size: 12px;
                    color: #fff;
                    margin-top: 12px;
                    word-wrap: break-word;
                    word-break: normal;
                    width: calc(100% - 51px);
                    height: 100%;
                }
            }
            .imgq{
                position: absolute;
                left: -22px;
                top: 5px;
            }
            .obList{
                width: 100%;
                border-radius: 0 0 8px 8px;
                li{
                    line-height: 32px;
                    padding-left: 10px;
                    font-size: 14px;
                    list-style: none;
                    border-top: 1px solid #ccc;
                }
                li:hover{
                    color: #2f86f7;
                }
            }
        }
        .headPortrait{
            width: 35px;
            margin: 10px 12px;
        }
        .nav{
            li{
                width: 60px;
                height: 60px;
                text-align: center;
                overflow: hidden;
                cursor: pointer;
                img{
                    width: 20px;
                    height: 20px;
                    margin: 10px 20px 0 20px;
                    display: block;
                }
                span{
                    color: #fff;
                    font-size: 13px;
                }
            }
            .ass{
                background: #02b980;
            }
        }
    }
    .right_body{
        width: calc(100% - 60px);
        height: 100%;
        float: left;
        background: #e6e6e6;
        overflow: auto;
    }
}
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 30px;
    height: 30px;
    line-height: 30px;
    text-align: center;
  }
  .avatar {
    width: 30px;
    height: 30px;
    display: block;
  }
</style>
